import React from 'react'

// Components
import Edit from './DialogContentTypes/Edit/Edit';
import Leaderboard from './DialogContentTypes/Leaderboard/Leaderboard';
import Stop from './DialogContentTypes/Stop/Stop.js';

// Card Types
import cardTypes from '../../cardTypes';

// Material UI Components
import { makeStyles, AppBar, Toolbar, Typography, Tooltip, Button, Icon, Dialog, DialogTitle, IconButton } from '@material-ui/core';

// Material UI Custom Component Style
const useStyles = makeStyles(theme => ({
    AppBar: {
        position: 'relative',
        zIndex: 10,
        background: theme.palette.background.default
    },
    Toolbar: {
        display: 'flex',
        justifyContent: 'space-between',
    },
    Icon: {
        width: '100%'
    },
    Orange: {
        color: '#F39C12'
    },
    Yellow: {
        color: '#F1C40F'
    },
    Teal: {
        color: '#1ABC9C'
    },
    Red: {
        color: '#E74C3C'
    },
    Button: {
        color: theme.palette.error.main
    },
}));

export default function Header( { cardType, gameState, leaderboard, onClickSelectCard, handleGetSeconds, onClickClearLeaderboard } ) {
    const Style = useStyles();
    const [openDialog, setOpenDialog] = React.useState(false);
    const [dialogType, setDialogType] = React.useState(null);

    let [randomIcon, setRandomIcon] = React.useState(null);

    const [startTimer, setStartTimer] = React.useState(false);
    let [time, setTime] = React.useState(0);

    // This method will open the dialog and set the dialog content to the type
    const handleClickDialogOpen = (type) => {
        setOpenDialog(true);
        setDialogType(type)
    };
    
    // This method will close the dialog 
    const handleDialogClose = () => {
        setOpenDialog(false);
        setRandomIcon(null);
    };

    const random = Math.floor(Math.random() * Math.floor(15));

    React.useEffect(() => {
        let timerinterval;
        let iconinterval;
        let seconds = 5000;
        
        if(gameState !== "Started"){
            iconinterval = setInterval(() => {
                const newRandom = Math.floor(Math.random() * Math.floor(15));
                setRandomIcon(() => {
                    return(
                        cardType === null 
                            ? `fas ${cardTypes[0].icons[newRandom]}`
                                : cardType.title === "Brands"
                                    ? `fab ${cardType.icons[newRandom]}`
                                        : `fas ${cardType.icons[newRandom]}`
                )})
            }, randomIcon === null ? 50 : seconds);
        }

        if(gameState === "Started"){
            setTimeout(() => {
                setStartTimer(true)
            }, 5000);
        }else if(gameState === "Stop"){
            setTimeout(() => {
                setStartTimer(false)
                handleGetSeconds(time);
            }, 0);
        }

        if(startTimer === true){
            timerinterval = setInterval(() => {
                setTime(newTime => newTime + 1);
            }, 1000); 
        }

        return () => {clearInterval(iconinterval); clearInterval(timerinterval);};
    }, [gameState, setRandomIcon, cardType, random, randomIcon, startTimer, setTime, time, handleGetSeconds]);

    return (
        <React.Fragment>
            <AppBar className={Style.AppBar} color="transparent" position="static" variant="outlined" square>
                <Toolbar className={Style.Toolbar}>
                    {
                        gameState === "Started" || gameState === "Stop"
                            ? 
                            <Typography color="primary" variant="h6">{time}<span>s</span></Typography>
                                :
                                <Tooltip title="Change Card Type" placement="right">
                                    <span>
                                        <IconButton aria-label="Change Card Type" color="primary" variant="text" edge="start" onClick={() => handleClickDialogOpen("Edit")}>
                                            <Icon 
                                                className={randomIcon} 
                                                style={{width: 'auto'}} 
                                                color="primary" />
                                        </IconButton>
                                    </span>
                                </Tooltip>
                    }
                    <Button aria-label="Logo" color="inherit" disableFocusRipple size="large" variant="text">
                        <span className={Style.Orange}>Con</span>
                        <span className={Style.Yellow}>cen</span>
                        <span className={Style.Teal}>tra</span>
                        <span className={Style.Red}>tion</span>
                    </Button>
                    <Tooltip title={gameState === "Started" || gameState === "Stop" ? "Stop Game" : "Leaderboard"} placement="left">
                        <span>
                            {
                                gameState === "Started" || gameState === "Stop"
                                    ? 
                                        <IconButton aria-label="Stop Game" className={Style.Button} variant="text" edge="end" onClick={() => handleClickDialogOpen("Stop")}>
                                            <Icon className={`${Style.Icon} fas fa-ban`} />
                                        </IconButton>
                                        : 
                                        <IconButton aria-label="Leaderboard" color="secondary" variant="text" edge="end" onClick={() => handleClickDialogOpen("Leaderboard")}>
                                            <Icon className={`${Style.Icon} fas fa-trophy`} />
                                        </IconButton>
                            }
                        </span>
                    </Tooltip>
                </Toolbar>
            </AppBar>
            <Dialog
                open={openDialog}
                onClose={handleDialogClose}
                aria-labelledby="dialogTitle"
            >
                <DialogTitle id="dialogTitle">
                    {
                        dialogType === "Edit" 
                            ? "Change Card Type"
                                : dialogType === "Leaderboard" 
                                    ? "Leaderboard"
                                        : dialogType === "Stop" 
                                            ? "Stop Game"
                                                : ""
                    }
                </DialogTitle>
                {
                    dialogType === "Edit" 
                        ? <Edit cardType={cardType} cardTypes={cardTypes} random={Math.floor(Math.random() * Math.floor(9))} onClickSelectCard={onClickSelectCard} handleDialogClose={handleDialogClose} />
                            : dialogType === "Leaderboard" 
                                ? <Leaderboard leaderboard={leaderboard} handleDialogClose={handleDialogClose} onClickClearLeaderboard={onClickClearLeaderboard} />
                                    : dialogType === "Stop" 
                                        ? <Stop leaderboard={leaderboard} handleDialogClose={handleDialogClose} onClickClearLeaderboard={onClickClearLeaderboard} />
                                            : ""
                }
            </Dialog>
        </React.Fragment>
    )
}
