import React from 'react';
import PropTypes from 'prop-types';
import CssBaseline from '@material-ui/core/CssBaseline';
import { createMuiTheme } from '@material-ui/core/styles';
import {HashRouter as Router, Switch, Route} from "react-router-dom";

// Components
import Start from '../components/Start/Start';
import Header from '../components/Header/Header';
// import Selection from '../components/Selection/Selection';
import Game from '../components/Game/Game';

// Material UI Components
import { ThemeProvider, Container, Fade } from '@material-ui/core';

// Style sheet
import Style from './App.module.scss';

// Material UI Custom Theme
const theme = createMuiTheme({
  palette: {
    primary: {
      light: '#85C1E9',
      main: '#3498DB',
      dark: '#2874A6',
    },
    secondary: {
      light: '#F7DC6F',
      main: '#F1C40F',
      dark: '#B7950B',
    },
    success: {
      light: '#82E0AA',
      main: '#2ECC71',
      dark: '#239B56',
    },
    error: {
      light: '#F1948A',
      main: '#E74C3C',
      dark: '#B03A2E',
    },
  },
});

export default class App extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      cardType: null,
      gameState: '',
      gameTime: null,
      leaderboard: [],
    }
  }

  static propTypes = {
    cardType: PropTypes.object,
    gameState: PropTypes.string,
    gameTime: PropTypes.number,
    leaderboard: PropTypes.array,
  }

  componentDidMount(){
    // Will set a variables with the value of the local storage 
    const setLeaderboard = JSON.parse(localStorage.getItem("Leaderboard") || "[]");

    this.setState({
      leaderboard: setLeaderboard,
    })
  }

  // This method will store the current phase of the application in the gameState state
  handleGame = (phase) => {
    console.log(phase)
    this.setState({
      gameState: phase
    })
  }

  // This method will store the icon array in the cardType state
  handleClickSelectCard = (card) => {
    this.setState({
      cardType: card
    })
  }

  // This method will store the seconds in the gameTime state
  handleGetSeconds = (time) => {
    this.setState({
      gameTime: time
    })
  }

  /* 
    This method will create an object that holds the name of the player and the number of seconds it took the player to match all cards
    Then it will store the object in the leaderboard array
  */ 
  handleClickEnterName = (name) => {
    if(name === ""){
      return
    }else{
      const time = this.state.gameTime;
      const newLeaderboard = [...this.state.leaderboard];

      const newItem = {
        id: Math.random(),
        name: name,
        time: time
      };

      newLeaderboard.push(newItem);

      newLeaderboard.sort((a, b) => parseFloat(a.time) - parseFloat(b.time));

      // Setting the leaderboard array into our local storage
      localStorage.setItem("Leaderboard", JSON.stringify(newLeaderboard));

      this.setState({
        cardType: null,
        gameState: '',
        gameTime: null,
        leaderboard: newLeaderboard,
      })
    }
  }

  // This method will clear the leaderboard
  handleClickClearLeaderboard = () => {
    // Deleting the Leaderboard item from local storage
    localStorage.removeItem("Leaderboard")

    this.setState({
      leaderboard: []
    })
  }

  render(){
    return (
      <React.Fragment>
        <CssBaseline />
        <ThemeProvider theme={theme}>
          <Router>
            <Container className={Style.Container} maxWidth="md" disableGutters>
              <Switch>
                <Route path="/game">
                  <Fade in timeout={2000}>
                    <div>
                      <Header 
                        cardType={this.state.cardType} 
                        gameState={this.state.gameState} 
                        leaderboard={this.state.leaderboard} 
                        onClickSelectCard={this.handleClickSelectCard} 
                        handleGetSeconds={this.handleGetSeconds} 
                        onClickClearLeaderboard={this.handleClickClearLeaderboard} />
                      <Game 
                        cardType={this.state.cardType} 
                        handleGame={this.handleGame} 
                        onClickEnterName={this.handleClickEnterName} />
                    </div>
                  </Fade>
                </Route>
                <Route exact path="/">
                  <Start />
                </Route>
              </Switch>
            </Container>
          </Router>
        </ThemeProvider>
      </React.Fragment>
    );
  }
}